import { Component, OnInit, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { DataService } from 'src/app/_services/data.services';
declare var $: any;
@Component({
  selector: 'app-Searchresult-cmp',
  templateUrl: './Searchresult.component.html',
  styleUrls: ['./Searchresult.component.scss']
})

export class SearchresultComponent implements OnInit, OnDestroy {
  search = null;
  city = null;
  test: Date = new Date();
  step = 0;
  incval = 0;
  incval1 = 0;
  incval2 = 0;
  incval3 = 0;
  incval4 = 0;
  user = null;
  languages = null;
  features = null;

  property_type = [];
  feature_type = [];
  language_type = [];
  min_price = null;
  max_price = null;

  campaignOne: FormGroup;
  campaignTwo: FormGroup;
  constructor(private router: Router,
    private dataService: DataService,
    private cdf: ChangeDetectorRef,) {
    const today = new Date();
    const month = today.getMonth();
    const year = today.getFullYear();

    this.campaignOne = new FormGroup({
      start: new FormControl(new Date(year, month, 13)),
      end: new FormControl(new Date(year, month, 16))
    });

    this.campaignTwo = new FormGroup({
      start: new FormControl(new Date(year, month, 15)),
      end: new FormControl(new Date(year, month, 19))
    });

    this.min_price = 50;
    this.max_price = 500;
  }

  ngOnInit() {
    if (localStorage.getItem("token")) {
      this.user = 'exists';
    }
    this.dataService.get('get-all-languages').then((result) => {
      this.languages = result.data
    })

    this.dataService.get('get-features').then((result) => {
      this.features = result.data
    })
    const body = document.getElementsByTagName('body')[0];
    body.classList.add('lock-page');
    body.classList.add('off-canvas-sidebar');
    const card = document.getElementsByClassName('card')[0];
    this.city = localStorage.getItem('city')
    this.search = JSON.parse(localStorage.getItem('data'))
    for (let i = 0; i < this.search.length; i++) {
      let totalPrice = this.search[i]['price']
      let price = totalPrice * (this.search[i]['discount_of_percentage'] / 100)
      price = totalPrice - price;
      this.search[i]['current_price'] = price;
      this.search[i]['lat'] = parseFloat(this.search[i]['lat'])
      this.search[i]['lan'] = parseFloat(this.search[i]['lan'])
    }

    this.dataService.get('get-all-languages').then((result) => {
      this.languages = result.data
    })

    this.dataService.get('get-features').then((result) => {
      this.features = result.data
    })
  }

  ngOnDestroy() {
    const body = document.getElementsByTagName('body')[0];
    body.classList.remove('lock-page');
    body.classList.remove('off-canvas-sidebar');
  }

  book(){
    location.href = "#/guest/offerdetails";
  }

  hidecollaps(val) {
    if (val == 1) {
      $('#collapseExample1').removeClass("show");
    }
    else if (val == 2) {
      $('#collapseExample').removeClass("show");

    }
    else {
      $('#collapseExample').removeClass("show");
      $('#collapseExample1').removeClass("show");

    }
  }

  plus(val) {
    switch (val) {
      case 0:
        this.incval += 1;
        break;
      case 1:
        this.incval1 += 1;
        break;
      case 2:
        this.incval2 += 1;
        break;
      case 3:
        this.incval3 += 1;
        break;
      case 4:
        this.incval4 += 1;
        break;

    }
  }

  minus(val) {
    switch (val) {
      case 0:
        this.incval -= 1;
        break;
      case 1:
        this.incval1 -= 1;
        break;
      case 2:
        this.incval2 -= 1;
        break;
      case 3:
        this.incval3 -= 1;
        break;
      case 4:
        this.incval4 -= 1;
        break;

    }
  }

  getOfferDetails(id){
    this.router.navigate(['guestportal/offerdetails', id])
  }

  advancedSearch(){
    $('.bd-example-modal-md').modal('hide');
    if(this.min_price > this.max_price){
      let temp = this.max_price
      this.max_price = this.min_price
      this.max_price = temp
    }

    const data = {
      destination: this.city,
      from: localStorage.getItem('from'),
      to: localStorage.getItem('to'),
      rooms: localStorage.getItem('rooms'),
      guests: localStorage.getItem('guests'),
      property_type: this.property_type,
      feature_type: this.feature_type,
      language_type: this.language_type,
      min_price: this.min_price,
      max_price: this.max_price
    }

    this.dataService.post('advanced-search', data).then((result) => {
      this.search = result.data[data['destination']];
      for (let i = 0; i < this.search.length; i++) {
        let totalPrice = this.search[i]['price']
        let price = totalPrice * (this.search[i]['discount_of_percentage'] / 100)
        price = totalPrice - price;
        this.search[i]['current_price'] = price;
        this.search[i]['lat'] = parseFloat(this.search[i]['lat'])
        this.search[i]['lan'] = parseFloat(this.search[i]['lan'])
      }
      this.cdf.detectChanges()
    })
  }

  property(e: any, property){
    if(e.source.checked){
      this.property_type.push(property)
    }
    else{
      this.property_type.splice(this.property_type.indexOf(property),1);
    }
  }

  feature(e: any, feature){
    if(e.source.checked){
      this.feature_type.push(feature)
    }
    else{
      this.feature_type.splice(this.feature_type.indexOf(feature),1);
    }
  }

  language(e: any, language){
    if(e.source.checked){
      this.language_type.push(language)
    }
    else{
      this.language_type.splice(this.language_type.indexOf(language),1);
    }
  }

  minSlider(){
    this.min_price = $('#minSlider').val();
  }

  maxSlider(){
    this.max_price = $('#maxSlider').val();
  }
}
